import React, {useState} from 'react'
import firebase from '../firebase/'


const AdminPanel = ({onSendInfo}) => {

    const [name, setName] = useState('')
    const [color, setColor] = useState('')
    const [price, setPrice] = useState('')
    const [image, setImage] = useState('')

    if(firebase.getCurrentUserUid() === 'yt22jtNlWVhf0sWJcAR3ElKHcst2') {
        //admin logged in
        return (
            <div className="admin-page">
            <p>Введите данные для добавления нового товара</p>
            <form 
            className="form"
            onSubmit = {e => e.preventDefault() && false}>
            <label>
                <div>Название</div>
                <input id="name" name="name" type="text" autoComplete="off" autoFocus value={name} onChange={((e) => setName(e.target.value))}/>
            </label>
            <label>
                <div>Цвет</div>
                <input id="color" name="color" type="text" autoComplete="off"  value={color} onChange={((e) => setColor(e.target.value))}/>
            </label>
            <label>
                <div>Цена за метр</div>
                <input id="price" name="price" type="number" autoComplete="off"  value={price} onChange={((e) => setPrice(e.target.value))}/>
            </label>
            <label>
                <div>Картинка</div>
                <input id="image" name="image" type="text" autoComplete="off" value={image} onChange={((e) => setImage(e.target.value))}autoFocus />
            </label>
            <button 
            className="form-button"
            type="submit"
            onClick={() => onSendInfo(name, color, price, image)}
            >Добавить
            </button>
            </form>
            </div>
        )
    }
    return null
    
}
export default AdminPanel