import React, {useState} from 'react'
import {Link, withRouter} from 'react-router-dom'
import './pages.css'
import firebase from '../firebase'
const LoginPage = (props) => {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [formClass, setFormClass] = useState('form')
    return (
        <div className="login-page">
            <h2 className="form-header">Вход</h2>
            <form 
            className={formClass}
            onSubmit = {e => e.preventDefault() && false}>
                <label className="form-field">
                    <div>E-mail</div>
                    <input id="e-mail" name="e-mail" autoComplete="off" autoFocus value={email} onChange={((e) => setEmail(e.target.value))} />
                </label>
                <label className="form-field">
                    <div>Пароль</div>
                    <input id="password" name="password" autoComplete="off" value={password} onChange={((e) => setPassword(e.target.value))}/>
                </label>
                <div className="form-field">
                <button 
                className="form-button"
                type="submit"
                onClick={login}
                >Войти
                </button>
                <Link to="/register" className="form-button">Зарегистрироваться</Link>
                </div>
            </form>
        </div>
    )
    async function login() {
        try {
            await firebase.login(email, password)
            props.history.replace('/cart')

        } catch(err) {
            if(err.code === "auth/invalid-email"){
                setFormClass('form alert')
            }
            
        }
    }
}

export default withRouter(LoginPage)