import React, { Component } from 'react'
import firebase from '../firebase'
import {Link} from 'react-router-dom'


export default class OrdersPage extends Component{
    onDelete = (key) => {
        firebase.removeOrder(key).then(() => firebase.getOrders().then(orders => this.setState({orders: orders})))
    }
    state = {
        orders: []
    }
    componentDidMount() {
        firebase.getOrders().then(orders => this.setState({orders: orders}))
    }
    renderItems = (item) => {
        let key = item.id*item.width*item.height*item.price*Math.floor(Math.random() * 1000)
        return (
            <table key = {key+ 1}>
                <thead>
                        <tr>
                            <th>Название</th>
                            <th>Ширина</th>
                            <th>Высота</th>
                            <th>Количетво</th>
                            <th>Стоимость</th>
                        </tr>
                </thead>
                <tbody>
                    <tr key={key}>
                        <td>{item.name}</td>
                        <td>{item.width}</td>
                        <td>{item.height}</td>
                        <td>{item.count}</td>
                        <td>{item.total}</td>
                    </tr>
                </tbody>
            </table>
        )
    }
    renderRow = (item) => {
        const {key, name, email, order, total} = item;
        
                        return (
                            <tr key={key}>
                                <td>{name}</td>
                                <td>{email}</td>
                                <td>{order.map(this.renderItems)}</td>
                                <td>{total}</td>
                                <td><button 
                                onClick = {() => this.onDelete(key)}>Удалить</button></td>
                            </tr>
                        )
    }

render() {
    const orders = this.state.orders
    if(firebase.getCurrentUserUid() === 'yt22jtNlWVhf0sWJcAR3ElKHcst2') {
        return (
            <table className="table table-responsive-sm">
                        <thead>
                            <tr>
                            <th>Имя</th>
                            <th>E-mail</th>
                            <th>Заказ</th>
                            <th>Сумма</th>
                            </tr>
                        </thead>
                        <tbody>
                            
                            {orders.map(this.renderRow)}
                            
                        </tbody>
                    </table>
            
        )
    }
    return (
        <Link to="login" className="btn btn-primary btn-large">Залогиниться</Link>
        
    )
}
    
}

