import React, {useState} from 'react'
import {Link, withRouter} from 'react-router-dom'
import './pages.css'
import firebase from '../firebase'
const RegisterPage = (props) => {

    const [name, setName] = useState('')
    const [password, setPassword] = useState('')
    const [email, setEmail] = useState('')
    return (
        <div className="login-page">
        <h2 className="form-header">Регистрация</h2>
        <form 
        className="form"
        onSubmit = {e => e.preventDefault() && false}>
            <label className="form-field">
                <div>Имя</div>
                <input id="name" name="name" autoComplete="off" autoFocus value={name} onChange={((e) => setName(e.target.value))}/>
            </label>
            <label className="form-field">
                <div>E-mail</div>
                <input id="e-mail" name="e-mail" autoComplete="off" value={email} onChange={((e) => setEmail(e.target.value))}/>
            </label>
            <label className="form-field">
                <div>Пароль</div>
                <input id="password" name="password" autoComplete="off" value={password} onChange={((e) => setPassword(e.target.value))}/>
            </label>
            <div className="form-field">
            <button 
            className="form-button"
            type="submit"
            onClick={onRegister}
            >Зарегистрироваться
            </button>
            <Link to="/login" className="form-button">Войти</Link>
            </div>
        </form>
    </div>
    )

    async function onRegister() {
        try {
            await firebase.register(name, email, password)
            props.history.replace('/cart')
        } catch (error) {
            alert(error.message)
        }
    }
}

export default withRouter(RegisterPage)