import React, {useState, useEffect} from 'react'
import ErrorBoundary from '../error-boundary/'
import { Route, Switch} from 'react-router-dom'
import {HomePage, CartPage, LoginPage, RegisterPage, SuccessPage, OrdersPage} from '../pages/'
import ShopHeader from '../shop-header'
import {connect} from 'react-redux'
import Spinner from '../spinner'
import firebase from '../firebase'
const App = ({orderTotal, cartItemsTotal}) =>{

    const [firebaseInitialized, setFirebaseInitialized] = useState(false)

    useEffect(() => {
        firebase.isInitialized().then(val => {
            setFirebaseInitialized(val)
        })
    })


        return firebaseInitialized !== false ? (
            <main role="main" className="container">
            <ShopHeader numItems={cartItemsTotal} total={orderTotal}/>
            <ErrorBoundary>
                <Switch>
                    <Route path="/re-store" component={HomePage} exact/>
                    <Route path="/cart" component={CartPage}/>
                    <Route path="/login" component={LoginPage}/>
                    <Route path="/register" component={RegisterPage}/>
                    <Route path="/success" component={SuccessPage}/>
                    <Route path="/orders" component={OrdersPage}/>
                </Switch>  
            </ErrorBoundary>
            </main>
        ) : <Spinner/>  
}
const mapStateToProps = ( {shoppingCart: {orderTotal, cartItemsTotal}}) => {
    return{orderTotal, cartItemsTotal}
}
export default connect(mapStateToProps)(App)