import React from 'react'
import {Link, withRouter} from 'react-router-dom'
import './shop-header.css'
import firebase from '../firebase'

const ShopHeader = (props) => {
    const {numItems, total} = props
    if(!firebase.getCurrentUsername()) {
        //not logged in
        return (
            <header className="shop-header row">
            <Link className="logo text-dark" to="/re-store">ReStore</Link>
            <Link to="/cart" className="shopping-cart">
                <i className="cart-icon fa fa-shopping-cart"/>
                {numItems} Предметов ({total} Р.)
            </Link>
            <div className="additional-pages">
            <Link to="/login">Войти</Link>
            <Link to="/register">Зарегистрироваться</Link>
            </div>
        </header>
        )
    } else if (firebase.getCurrentUserUid() === 'yt22jtNlWVhf0sWJcAR3ElKHcst2') {
        return(
            <header className="shop-header row">
                <Link className="logo text-dark" to="/re-store">ReStore</Link>
                <Link to="/cart" className="shopping-cart">
                    <i className="cart-icon fa fa-shopping-cart"/>
                    {numItems} Предметов ({total} Р.)
                </Link>
                <div className="additional-pages">
                <Link to="/orders">Заказы</Link>
                <button className="logout" onClick={logout} type="submit">Выйти</button>
                </div>
            </header>
        )
    }
    return(
        <header className="shop-header row">
            <Link className="logo text-dark" to="/re-store">ReStore</Link>
            <Link to="/cart" className="shopping-cart">
                <i className="cart-icon fa fa-shopping-cart"/>
                {numItems} Предметов ({total} Р.)
            </Link>
            <div className="additional-pages">
            <button className="logout" onClick={logout} type="submit">Выйти</button>
            </div>
        </header>
    )
    async function logout() {
        await firebase.logout()
        props.history.push('/re-store')
    }
}
export default withRouter(ShopHeader)