  class Firebase {
      constructor () {
        app.initializeApp(config)
        this.auth = app.auth()
        this.db = app.firestore()
      }
      login(email, password) {
          return this.auth.signInWithEmailAndPassword(email, password)
      }
      logout() {
          return this.auth.signOut()
      }
      async register(name, email, password) {
          await this.auth.createUserWithEmailAndPassword(email, password)
          return this.auth.currentUser.updateProfile({
              displayName: name
          })
      }

      isInitialized() {
          return new Promise (resolve => {
              this.auth.onAuthStateChanged(resolve)
          })
      }
      getCurrentUsername() {
            if(this.auth.currentUser)
            return this.auth.currentUser.displayName
      }
      getCurrentUserEmail() {
            if(this.auth.currentUser)
            return this.auth.currentUser.email
      }
      getCurrentUserUid() {
          if(this.auth.currentUser)
          return this.auth.currentUser.uid
      }
      getMaxOfArray(numArray) {
        return Math.max.apply(null, numArray);
      }
      async getMaxId() {
        return await this.db.collection("frames").get().then((querySnapshot) => {
            const data = []
             querySnapshot.forEach((doc) => {
                data.push(doc.data().id) 
            });
            return data
        }).then(data => this.getMaxOfArray(data) + 1)
    }
    async getFrames() {   

        return await this.db.collection("frames").get().then((querySnapshot) => {
            const data = []
             querySnapshot.forEach((doc) => {
                 const key = doc.id;
                 let object = doc.data()
                 object.key = key
                data.push(object) 
            });
            return data
        })
    }
    async getOrders() {
        return await this.db.collection("orders").get().then((querySnapshot) => {
            const data = []
             querySnapshot.forEach((doc) => {
                 const key = doc.id;
                 let object = doc.data()
                 object.key = key
                data.push(object) 
            });
            return data
        })
    }
    
    SendInfo = async (name, color, price, image) => {
        const key = await this.getMaxId()
        const id = await this.getMaxId()
        await this.db.collection("frames").add({
            key: key,
            id: id,
            name: name,
            color: color,
            price: price,
            width: 20,
            height: 20,
            image: image
        })
        return new Promise((resolve,reject) => {
            resolve(console.log("Document successfully sent!"))
        })
    }
    sendOrder = async(name, email, order, total) => {
        await this.db.collection("orders").add({
            name: name,
            email: email,
            order: order,
            total: total
        })
        return new Promise((resolve,reject) => {
            resolve(console.log("Document successfully sent!"))
        })
    }
    removeItem = async (key) => {
        await this.db.collection("frames").doc(key).delete()
        return new Promise((resolve,reject) => {
            resolve(console.log("Document successfully deleted!"))
        })
    }
    removeOrder = async (key) => {
        await this.db.collection("orders").doc(key).delete()
        return new Promise((resolve,reject) => {
            resolve(console.log("Document successfully deleted!"))
        })
    }
  }

  export default new Firebase()